import { ReactNode } from 'react';

import clsx from 'clsx';

import { Avatar, Button, Tooltip } from '../../ui';
import { TComponentSizes } from '../types';

import styles from './AvatarText.module.scss';

import { UsersIcon } from '../../assets/icons';

type AvatarTextComponentProps = {
    src: string | null;
    text: string;
    subtext?: ReactNode;
    subtextStyle?: 'default' | 'warning';
    isMultiOwners?: boolean;
    icon?: any;
    size?: TComponentSizes;
    onClick?: () => void;
    sourcePostSrc?: string;
    className?: string;
};

const AvatarText = ({
    src,
    text,
    subtext,
    subtextStyle = 'default',
    icon,
    isMultiOwners = false,
    size = 'large',
    onClick,
    sourcePostSrc,
    className,
}: AvatarTextComponentProps) => {
    // const router = useRouter();

    return (
        <figure
            role={onClick !== undefined ? 'button' : undefined}
            tabIndex={onClick !== undefined ? 0 : undefined}
            className={clsx(styles.avatarText, className)}
            onClick={onClick}
            onKeyDown={onClick}
        >
            <Avatar src={src} alt={text} iconType={icon} size={size} />
            <figcaption className={styles.avatarTextCaption}>
                <p className={styles.avatarTextName}>{text}</p>
                {subtext !== undefined && subtext !== null && (
                    <small
                        className={clsx(
                            styles.avatarTextSubtext,
                            subtextStyle === 'default'
                                ? styles.subtextDefault
                                : styles.subtextWarning,
                        )}
                    >
                        {subtext}
                    </small>
                )}
            </figcaption>

            {isMultiOwners && (
                <Tooltip
                    tooltipContent="    "
                    classes={{ root: styles.tooltipContainer, tooltip: styles.tooltip }}
                >
                    <UsersIcon
                        aria-label="    "
                        className={styles.usersIcon}
                    />
                </Tooltip>
            )}

            {/* {sourcePostSrc !== undefined && (
                <Button
                    text="  "
                    size="small"
                    onClick={() => router.push(sourcePostSrc)}
                />
            )} */}
        </figure>
    );
};

export default AvatarText;
