import React from 'react';

import clsx from 'clsx';

import styles from './LineAlert.module.scss';

import {
    ClockIcon,
    ErrorIcon,
    ExclaimationCircleIcon,
    LampIcon,
    SuccessIcon,
} from '../../assets/icons';

export type AlertStatus = 'success' | 'info' | 'warning' | 'error' | 'time';
const mapStatusToClass: Record<AlertStatus, string> = {
    success: styles.statusSuccess,
    warning: styles.statusWarning,
    error: styles.statusError,
    info: styles.statusInfo,
    time: styles.statusInfo,
};

type AlertProps = {
    status: AlertStatus;
    text: string;
};

const LineAlert = ({ status, text }: AlertProps) => {
    const icon = () => {
        switch (status) {
            case 'info':
                return <LampIcon />;
            case 'time':
                return <ClockIcon />;
            case 'warning':
                return <ExclaimationCircleIcon />;
            case 'error':
                return <ErrorIcon />;
            case 'success':
            default:
                return <SuccessIcon />;
        }
    };

    return (
        <span className={clsx(styles.wrapper, mapStatusToClass[status])}>
            {icon()} {text}
        </span>
    );
};

export default LineAlert;
