import React from 'react';

import { SvgIcon } from '@/global-types';
import clsx from 'clsx';

import styles from './StaticNotification.module.scss';

import { CloseIcon, WarningIcon } from '../../assets/icons';

type StaticNotificationVariant = 'warning' | 'error' | 'neutral';

const foldVariantToClass: Record<StaticNotificationVariant, string> = {
    error: styles.isRed,
    neutral: styles.isGray,
    warning: styles.isYellow,
};

type StaticNotificationProps = {
    label: string;
    text?: string;
    variant?: StaticNotificationVariant;
    icon?: SvgIcon;
    onClose?(): void;
    className?: string;
};
const StaticNotification = (props: StaticNotificationProps) => {
    const {
        label,
        text,
        className,
        onClose,
        variant = 'warning',
        icon: Icon = WarningIcon,
    } = props;

    return (
        <div className={clsx(styles.root, foldVariantToClass[variant], className)}>
            <Icon className={styles.icon} />

            <div>
                <p className={styles.label}>{label}</p>
                {text !== undefined && <p className={styles.text}>{text}</p>}
            </div>

            {onClose !== undefined && (
                <button
                    type="button"
                    aria-label="Close notification"
                    className={clsx(text === undefined && styles.noText, styles.button)}
                    onClick={onClose}
                >
                    <CloseIcon />
                </button>
            )}
        </div>
    );
};

export default StaticNotification;
