type RequestMethodsTypes = 'get' | 'post' | 'put' | 'delete';

type ResponseErrorCheckType = 'donor-limit' | 'channel-limit';

export enum ProcessStatus {
  Process = 'process',
  Ready = 'ready',
  Error = 'error',
}

export type RequestHeaderItemType = {
  [key: string]: string;
};

export type ResponseErrorType = Record<string, string[]>;

export type SuccessResponseType<T = unknown> = {
  success: true;
  data: T;
  message?: string;
};

export type ErrorResponseType = {
  success: false;
  message?: string;
  errors?: ResponseErrorType;
};

export type ResponseType = any;

/**
 *     
 * @param formData as HelpRequestUnauthorized
 * */
export const makeHelpRequestUnauthorized = (
  formData: FormData
): Promise<ResponseType> => {
  return apiRequest(
    `/app/v1.2/api/publications/action/jira-servicedesk`,
    'post',
    formData
  );
};

/**
 *     
 * @param formData as HelpRequestAuthorized
 * */
export const makeHelpRequestAuthorized = (
  formData: FormData
): Promise<ResponseType> => {
  return apiRequest(
    `/app/v1.2/api/publications/action/jira-servicedesk-auth`,
    'post',
    formData
  );
};

export const apiRequest = async <
  T = ResponseType,
  D = Record<string, unknown> | FormData
>(
  url: string,
  method: RequestMethodsTypes = 'get',
  body?: D,
  headers?: RequestHeaderItemType[] | null
): Promise<T> => {
  const headersContent: RequestHeaderItemType = {
    Accept: 'application/json',
  };

  const isFormData = body instanceof FormData;
  if (isFormData) {
    headersContent.enctype = 'multipart/form-data';
  } else {
    headersContent['Content-Type'] = 'application/json';
  }

  if (!!getUserLocalBearerToken()) {
    headersContent.Authorization = `Bearer ${getUserLocalBearerToken()}`;
  }

  if (headers !== undefined && headers !== null && headers.length > 0) {
    headers.forEach((header) => {
      for (const item in header) {
        headersContent[item] = header[item];
      }
    });
  }

  const req =
    method === 'get'
      ? await fetch(requestPathResolver(url), {
          method,
          mode: 'cors',
          headers: headersContent,
          credentials: 'include',
        })
      : await fetch(requestPathResolver(url), {
          method,
          body: isFormData ? body : JSON.stringify(body),
          mode: 'cors',
          headers: headersContent,
          credentials: 'include',
        });

  //   if (req.status === 401 && !noAuthPages.includes(Router.pathname)) {
  //     const loginPath = makeRoute('Login', undefined, {
  //       [linkForRedirectParamKey]: Router.asPath,
  //       [userLogoutParamKey]: 1,
  //     });

  //     if (isServer()) {
  //       return redirect(loginPath);
  //     } else {
  //       setLocalStorageItem<TAuthPersistentState>(
  //         { accessToken: null },
  //         LS_AUTH_CREDS_KEY
  //       );

  //       await Router.push(loginPath);

  //       throw new Error(userLogoutMessage);
  //     }
  //   }

  return req.json();
};

const requestPathResolver = (url: string): string => url;

function getUserLocalBearerToken() {
  return '';
}
