import { ResponseType } from '@/helpers/apiRequest';

export type TErrorsList = Record<string, string>;

export const errorHandlers = (response: ResponseType): TErrorsList => {
    if (
        response.success ||
        response.errors === undefined ||
        Object.keys(response.errors).length === 0
    )
        return {};

    const errors: TErrorsList = {};

    for (const key in response.errors) {
        errors[key] = response.errors[key][0];
    }
    return errors;
};

export const hasError = (key: string, errors: TErrorsList): boolean =>
    key.length > 0 && Object.keys(errors).length > 0 && errors.hasOwnProperty(key);

export const getErrorMessage = (key: string, errors: TErrorsList): string | null => {
    if (key.length === 0 || Object.keys(errors).length === 0 || !errors.hasOwnProperty(key))
        return null;
    return errors[key];
};

export const mapResponseErrorsToString = (response: ResponseType, defaultErrorText = '') => {
    let errorText = defaultErrorText;

    const errors = errorHandlers(response);

    if (Object.keys(errors).length > 0) {
        errorText = Object.entries(errors)
            .map(([key, value]) => `${key}: ${value}`)
            .join('; ');
    }

    return errorText;
};
