import React, { DetailedHTMLProps, TextareaHTMLAttributes, useState } from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';
import TextareaAutosize from 'react-textarea-autosize';

import styles from './Textarea.module.scss';

type ClassKey = 'root' | 'input' | 'label';

export type TextareaBaseProps = {
    label?: string;
    subtext?: string;
    isError?: boolean;
    classes?: Classes<ClassKey>;
    isDisabled?: boolean;
};

type TextareaProps = TextareaBaseProps &
    DetailedHTMLProps<
        Omit<TextareaHTMLAttributes<HTMLTextAreaElement>, 'style'>,
        HTMLTextAreaElement
    >;

const Textarea = (props: TextareaProps) => {
    const {
        label,
        placeholder,
        subtext,
        isError = false,
        value,
        onInput,
        onBlur,
        onChange,
        className,
        classes,
        isDisabled,
        ...otherProps
    } = props;

    const [isLabelFloated, setFloatLabel] = useState(Boolean(value));

    return (
        <label className={clsx(styles.root, className, classes?.root)}>
            <TextareaAutosize
                className={clsx(styles.input, isError && styles.isError, classes?.input)}
                onFocus={(event) => {
                    setFloatLabel(true);
                    onInput?.(event);
                }}
                onBlur={(event) => {
                    setFloatLabel(false);
                    onBlur?.(event);
                }}
                onChange={onChange}
                value={value}
                placeholder={placeholder}
                disabled={isDisabled}
                {...otherProps}
            />

            {label !== undefined && (
                <span
                    className={clsx(
                        styles.label,
                        (isLabelFloated || Boolean(value)) && styles.isLabelFloated,
                        isError && styles.isError,
                        classes?.label,
                    )}
                >
                    {label}
                </span>
            )}

            {subtext !== undefined && (
                <small className={clsx(styles.subText, isError && styles.isError)}>{subtext}</small>
            )}
        </label>
    );
};

export default Textarea;
