import React from 'react';

import { Classes } from '../../global-types';
import clsx from 'clsx';

import { SquaredSpinner } from '../../ui';
import { TComponentSizes } from '../../ui/types';

import styles from './Buttons.module.scss';

type ButtonStyle = 'primary' | 'secondary' | 'outline' | 'link' | 'gray' | 'graySecondary';

const mapSizeToClass: Record<TComponentSizes, string> = {
    large: styles.buttonSizeLarge,
    base: styles.buttonSizeBase,
    small: styles.buttonSizeSmall,
};

const mapStyleToClass: Record<ButtonStyle, string> = {
    link: styles.buttonLinkColor,
    outline: styles.buttonOutlineColor,
    primary: styles.buttonPrimaryColor,
    secondary: styles.buttonSecondaryColor,
    gray: styles.buttonGrayColor,
    graySecondary: styles.buttonGraySecondaryColor,
};

export type ClassKey = 'root' | 'leadIconWrapper' | 'trailIconWrapper';

type ButtonProps = {
    text: string;
    buttonStyle?: ButtonStyle;
    onClick?: (event: React.MouseEvent<HTMLButtonElement>) => void;
    id?: string;
    size?: TComponentSizes;
    leadIcon?: React.ReactNode;
    trailIcon?: React.ReactNode;
    isLoading?: boolean;
    isDisabled?: boolean;
    classes?: Classes<ClassKey>;
} & React.ButtonHTMLAttributes<HTMLButtonElement>;

/**
 * Button
 * @description Button are interactive element activated by a user with a mouse, keyboard, or tap
 * @link https://wind-ui.com/components/buttons/
 * @param id
 * @param text
 * @param onClick
 * @param style
 * @param size
 * @param leadIcon
 * @param trailIcon
 * @param isLoading
 * @param isDisabled
 * @constructor
 */
const Button = ({
    id,
    text,
    onClick,
    buttonStyle = 'primary',
    size = 'base',
    leadIcon,
    trailIcon,
    className,
    classes,
    isLoading = false,
    isDisabled = false,
    ...otherProps
}: ButtonProps) => {
    return (
        <button
            id={id}
            type="button"
            onClick={onClick}
            className={clsx(
                styles.button,
                mapSizeToClass[size],
                mapStyleToClass[buttonStyle],
                className,
                classes?.root,
            )}
            disabled={isDisabled || isLoading}
            {...otherProps}
        >
            {leadIcon !== undefined && !isLoading && (
                <span className={clsx(styles.buttonIconWrapper, classes?.leadIconWrapper)}>
                    {leadIcon}
                </span>
            )}
            {isLoading && (
                <span className={styles.buttonLoader}>
                    <SquaredSpinner />
                </span>
            )}
            <span>{text}</span>
            {trailIcon !== undefined && (
                <span className={clsx(styles.buttonIconWrapper, classes?.trailIconWrapper)}>
                    {trailIcon}
                </span>
            )}
        </button>
    );
};

export default Button;
