import { createRoot } from 'react-dom/client';
import { HelpButton } from './src/components/HelpButton/HelpButton';
import React from 'react';

export function init(opts: INIT_OPTS) {
  const root = createRoot(opts.container); // createRoot(container!) if you use TypeScript
  root.render(<HelpButton />);
  return root;
}

export type INIT_OPTS = {
  container: HTMLElement;
};
