import React, { FormEvent, KeyboardEvent, useState } from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import { Spinner } from '..';

import styles from './Input.module.scss';

type ClassKey = 'root' | 'input';

type InputProps = {
    id: string;
    value: string;
    onInput?: (e: FormEvent<HTMLInputElement>) => void;
    onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
    onKeyDown?: (e: KeyboardEvent<HTMLInputElement>) => void;
    size?: 'base' | 'large';
    type?: 'text' | 'email' | 'password' | 'date' | 'time';
    label?: string;
    subtext?: string;
    isError?: boolean;
    isLoading?: boolean;
    disabled?: boolean;
    leadIcon?: React.ReactNode;
    trailIcon?: React.ReactNode;
    classes?: Classes<ClassKey>;
    isAutofillDisabled?: boolean;
    min?: number | string | undefined;
};

/**
 * @param id
 * @param value
 * @param onInput
 * @param placeholder
 * @param label
 * @param subtext
 * @param isError
 * @param type
 * @constructor
 */
const Input = ({
    id,
    value,
    onInput,
    onKeyDown,
    size = 'base',
    type = 'text',
    label,
    subtext,
    isError = false,
    isLoading = false,
    disabled = false,
    leadIcon,
    trailIcon,
    classes,
    isAutofillDisabled = false,
    ...otherProps
}: InputProps) => {
    const [isLabelFloated, setFloatLabel] = useState(Boolean(value));

    const [isReadOnly, setIsReadOnly] = useState(isAutofillDisabled);

    const onFocusHandler = (event: React.FocusEvent<HTMLInputElement>) => {
        setFloatLabel(true);
        setIsReadOnly(false);
        onInput?.(event);
    };

    const onBlurHandler = () => {
        setFloatLabel(false);
    };

    return (
        <label className={clsx(styles.root, classes?.root)}>
            <div
                className={clsx(
                    styles.wrapper,
                    size === 'base' ? styles.sizeBase : styles.sizeLarge,
                    isError ? styles.statusError : styles.statusDefault,
                    disabled && styles.disabled,
                )}
            >
                {label !== undefined && (
                    <div
                        className={clsx(
                            styles.labelWrapper,
                            size === 'large' && styles.isLarge,
                            (isLabelFloated || Boolean(value)) && styles.isLabelFloated,
                            isError ? styles.statusError : styles.statusDefault,
                        )}
                    >
                        <span className={styles.labelText}>{label}</span>
                    </div>
                )}
                {leadIcon !== undefined && !isLoading && (
                    <span className={styles.inputIconWrapper}>{leadIcon}</span>
                )}
                {isLoading && (
                    <span className={clsx(styles.inputIconWrapper, styles.loader)}>
                        <Spinner strokeWidth={1} />
                    </span>
                )}
                <input
                    id={id}
                    type={type}
                    value={value}
                    onInput={onInput}
                    onFocus={onFocusHandler}
                    onBlur={onBlurHandler}
                    disabled={disabled}
                    onKeyDown={onKeyDown}
                    className={clsx(classes?.input)}
                    readOnly={isReadOnly}
                    {...otherProps}
                />
                {trailIcon !== undefined && (
                    <span className={styles.inputIconWrapper}>{trailIcon}</span>
                )}
            </div>
            {subtext !== undefined && (
                <small className={clsx(styles.subText, isError && styles.isError)}>{subtext}</small>
            )}
        </label>
    );
};

export default Input;
