import clsx from 'clsx';

import styles from './Checkbox.module.scss';

import { CheckIcon } from '../../assets/icons';

type CheckboxProps = {
    isChecked: boolean;
    onChange?: (nextState: boolean) => void;
    className?: string;
    isDisabled?: boolean;
};

const Checkbox = ({ isChecked, onChange, className, isDisabled = false }: CheckboxProps) => {
    return (
        <button
            role="checkbox"
            aria-checked={isChecked}
            type="button"
            onClick={() => onChange?.(!isChecked)}
            disabled={isDisabled}
            className={clsx(
                styles.checkbox,
                isChecked ? styles.checked : styles.unchecked,
                className,
            )}
        >
            {isChecked && <CheckIcon />}
        </button>
    );
};

export default Checkbox;
