import React, { ReactNode, useEffect, useRef } from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import styles from './Modal.module.scss';

import { CloseIcon } from '@/assets/icons';

type ClassKey = 'root' | 'header' | 'title' | 'closeButton';

type ModalProps = {
  title?: string;
  isOpened: boolean;
  onClose?(): void;
  children?: ReactNode;
  classes?: Classes<ClassKey>;
};
const Modal = (props: ModalProps) => {
  const { title, isOpened, onClose, classes, children } = props;

  const rootRef = useRef<HTMLDialogElement | null>(null);

  const onChange = () => {
    const dialog = rootRef?.current ?? null;

    if (dialog === null) return;

    if (isOpened && !dialog.open) {
      dialog.showModal();
      document.body.classList.add('isScrollDisabled');
    } else {
      dialog.close();
      document.body.classList.remove('isScrollDisabled');
    }
  };

  useEffect(() => {
    onChange();
  }, [isOpened]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    return () => document.body.classList.remove('isScrollDisabled');
  }, []);

  return (
    <dialog ref={rootRef} className={clsx(styles.root, classes?.root)}>
      {(title !== undefined || onClose !== undefined) && (
        <div className={clsx(styles.header, classes?.header)}>
          {title !== undefined && (
            <h3 className={clsx(styles.title, classes?.title)}>{title}</h3>
          )}
          {onClose !== undefined && (
            <button
              type="button"
              aria-label="close modal"
              onClick={onClose}
              className={clsx(styles.closeButton, classes?.closeButton)}
            >
              {/* <CloseIcon className={styles.closeIcon} /> */}
            </button>
          )}
        </div>
      )}

      {children}
    </dialog>
  );
};

export default Modal;
