import React, { useEffect, useState } from 'react';

import { TVendorName } from '@/global-types';
import clsx from 'clsx';

import { TComponentSizes } from '@/ui/types';

import styles from './Avatar.module.scss';

import { TGIcon, VKIcon } from '../../assets/icons';

type AvatarComponentProps = {
    src: string | null;
    alt: string;
    iconType?: TVendorName | null;
    size?: TComponentSizes;
    className?: string;
};

const mapSizeToClass: Record<TComponentSizes, string> = {
    small: styles.sizeSmall,
    base: styles.sizeBase,
    large: styles.sizeLarge,
};

const Avatar = ({ src, alt, className, iconType = null, size = 'large' }: AvatarComponentProps) => {
    const [path, setPath] = useState<string | null>(null);

    const imageStubText = () =>
        alt
            .trim()
            .split(' ')
            .map((word) => word.at(0)?.toUpperCase())
            .filter((letter) => letter !== undefined)
            .slice(0, 2)
            .join('');

    useEffect(() => {
        setPath(src);
    }, [src]);

    return (
        <div className={clsx(styles.avatar, mapSizeToClass[size], className)}>
            <div className={styles.avatarWrapper}>
                {path !== null && (
                    <img src={path} alt={alt} loading="lazy" onError={() => setPath(null)} />
                )}
                {path === null && <small className={styles.imageStub}>{imageStubText()}</small>}
            </div>
            {iconType !== null && (
                <small className={styles.avatarSmallIcon}>
                    {iconType === 'tg' && <TGIcon />}
                    {iconType === 'vk' && <VKIcon />}
                </small>
            )}
        </div>
    );
};

export default Avatar;
