import React, { useState, useEffect } from 'react';

export function userUser1f() {
  const [user, setUser] = useState(null);
  useEffect(() => {
    function handleUserChange(user) {
      setUser(user);
    }

    const spaApi: any = window['spaApi'];
    const spaEvent: any = window['spaEvent'];

    function updateAuthData() {
      spaApi.getSessionUser().data.then((u) => {
        handleUserChange({
          name: u.name || '',
          email: u.email || u.externalEmail || '',
        });
      });
    }

    if (typeof spaApi !== 'undefined') {
      spaEvent('auth', updateAuthData);
      updateAuthData();
    }

    console.log('userUser1f');
    const to = setTimeout(() => {
      handleUserChange({
        name: 'test',
        email: 'email',
      });
    }, 3000);
    return () => {
      clearTimeout(to);
    };
  }, []);

  return {
    user,
    isAuthorized: !!user,
  };
}
