import React from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import styles from './Chips.module.scss';

import { CloseIcon } from '../../assets/icons';

type ClassKey = 'root' | 'chip' | 'chipLabel';

type ChipsProps = {
    chips: string[];
    onChipsChange(chips: string[]): void;
    classes?: Classes<ClassKey>;
    isError?: boolean;
    disabled?: boolean;
};
const Chips = (props: ChipsProps) => {
    const { chips, onChipsChange, classes, disabled = false, isError = false } = props;

    const deleteChip = (indexForDelete: number) => {
        onChipsChange(chips.filter((_chip, index) => index !== indexForDelete));
    };

    return (
        <ul className={clsx(styles.root, classes?.root)}>
            {chips.map((chip, index) => (
                <li
                    tabIndex={0}
                    key={index}
                    className={clsx(styles.chip, classes?.chip, isError ? styles.isError : null)}
                >
                    <p className={clsx(styles.label, classes?.chipLabel)}>{chip}</p>

                    <button
                        type="button"
                        aria-label="remove chip"
                        className={styles.button}
                        onClick={() => deleteChip(index)}
                        disabled={disabled}
                    >
                        <CloseIcon />
                    </button>
                </li>
            ))}
        </ul>
    );
};

export default Chips;
