import React from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import styles from './Toggle.module.scss';

type ClassKey = 'root' | 'button' | 'label' | 'description';

type ToggleProps = {
    isActive: boolean;
    onChange?(isActive: boolean): void;
    label?: string;
    description?: string;
    isDisabled?: boolean;
    classes?: Classes<ClassKey>;
};
const Toggle = (props: ToggleProps) => {
    const { isActive, onChange, label, description, isDisabled, classes } = props;

    return (
        <div className={clsx(classes?.root)}>
            <label className={styles.toggleSection}>
                <button
                    role="checkbox"
                    type="button"
                    aria-checked={isActive}
                    className={clsx(styles.button, isActive && styles.isActive, classes?.button)}
                    onClick={() => onChange?.(!isActive)}
                    disabled={isDisabled || onChange === undefined}
                />

                {label !== undefined && (
                    <span className={clsx(styles.label, classes?.label)}>{label}</span>
                )}
            </label>

            {description !== undefined && (
                <p className={clsx(styles.description, classes?.description)}>{description}</p>
            )}
        </div>
    );
};

export default Toggle;
