import clsx from 'clsx';
import { Lightbox, Slide } from 'yet-another-react-lightbox';
import { Thumbnails, Video } from 'yet-another-react-lightbox/plugins';
import 'yet-another-react-lightbox/plugins/thumbnails.css';
import 'yet-another-react-lightbox/styles.css';
import React from 'react';

import styles from './StyledLightbox.module.scss';

import { ChevronDownIcon, CloseIcon } from '../../assets/icons';

export const getLightboxControl = (amountOfSlide: number) =>
  amountOfSlide > 1 ? undefined : () => null;

type StyledLightboxProps = {
  isOpen: boolean;
  onClose(): void;
  attachments: any[];
  chosenIndex: number;
};
const StyledLightbox = (props: StyledLightboxProps) => {
  const { attachments, isOpen, onClose, chosenIndex = 0 } = props;

  const lightboxControl = getLightboxControl(attachments.length);

  const slides: Slide[] = attachments.map((attachment) => {
    if (attachment.type === 'video')
      return {
        type: 'video',
        sources: [
          {
            src: attachment.url,
            type: 'video/mp4',
          },
        ],
      };

    return { src: attachment.url };
  });

  return (
    <Lightbox
      open={isOpen && attachments.length > 0}
      close={onClose}
      index={chosenIndex}
      slides={slides}
      className={styles.root}
      styles={{
        slide: { '--yarl__carousel_padding_px': 42 },
      }}
      plugins={[Thumbnails, Video]}
      render={{
        buttonNext: lightboxControl,
        buttonPrev: lightboxControl,
        iconClose: () => <CloseIcon className={styles.icon} />,
        iconPrev: () => (
          <ChevronDownIcon className={clsx(styles.icon, styles.isPrev)} />
        ),
        iconNext: () => (
          <ChevronDownIcon className={clsx(styles.icon, styles.isNext)} />
        ),
      }}
      carousel={{
        finite: true,
        preload: 10,
      }}
      controller={{
        closeOnBackdropClick: true,
      }}
    />
  );
};

export default StyledLightbox;
