import React, { ReactNode, useEffect, useRef, useState } from 'react';

import { Classes, SvgIcon } from '@/global-types';
import clsx from 'clsx';

import { useClickOutside } from '../../helpers/useClickOutside';

import styles from './Select.module.scss';

import { ChevronDownIcon } from '../../assets/icons';

type ClassKey = 'root' | 'titleContainer' | 'title' | 'list' | 'option' | 'toggler';

export type SelectOptionValue = string | number;

export type SelectOption<Value extends SelectOptionValue> = { text: ReactNode; value: Value };

const selectId = 'select';

export type SelectProps<Value extends SelectOptionValue> = {
    items: SelectOption<Value>[];
    label?: string;
    selectedItem?: SelectOption<Value>;
    onChange?(value: SelectOption<Value>): void;
    isError?: boolean;
    subtext?: string;
    isDisabled?: boolean;
    classes?: Classes<ClassKey>;
    icon?: SvgIcon;
    listDirection?: 'bottom' | 'top';
};
const Select = <Value extends SelectOptionValue>(props: SelectProps<Value>) => {
    const {
        items,
        onChange,
        label,
        classes,
        icon: TogglerIcon = ChevronDownIcon,
        subtext,
        isDisabled = false,
        isError = false,
        selectedItem,
        listDirection = 'bottom',
    } = props;

    const ref = useRef<HTMLDivElement | null>(null);

    const [isVisible, setIsVisible] = useState(false);

    useClickOutside(ref, () => setIsVisible(false));

    const hasSelectedItem = selectedItem !== undefined;

    useEffect(() => {
        setIsVisible(false);
    }, [selectedItem]);

    return (
        <>
            <div className={clsx(styles.root, classes?.root)} ref={ref}>
                <button
                    id={selectId}
                    type="button"
                    disabled={isDisabled}
                    onClick={() => setIsVisible((prevIsVisible) => !prevIsVisible)}
                    className={clsx(
                        styles.titleContainer,
                        isVisible && styles.isActive,
                        isError && styles.isError,
                        classes?.titleContainer,
                    )}
                >
                    {label !== undefined && (
                        <label
                            htmlFor={selectId}
                            aria-label="label"
                            className={clsx(
                                styles.label,
                                hasSelectedItem && styles.isLabelFloated,
                                isError && styles.isError,
                            )}
                        >
                            {label}
                        </label>
                    )}

                    {hasSelectedItem && (
                        <p
                            aria-label="selected value"
                            className={clsx(styles.title, classes?.title)}
                        >
                            {selectedItem.text}
                        </p>
                    )}

                    {TogglerIcon !== undefined && (
                        <TogglerIcon
                            className={clsx(
                                styles.toggler,
                                isVisible && styles.isRotated,
                                classes?.toggler,
                            )}
                        />
                    )}
                </button>
                {isVisible && (
                    <ul
                        className={clsx(
                            styles.list,
                            listDirection === 'top' && styles.isTopDirection,
                            classes?.list,
                        )}
                    >
                        {items.map((item, index) => (
                            <li key={index}>
                                <button
                                    type="button"
                                    className={clsx(styles.option, classes?.option)}
                                    disabled={selectedItem?.value === item.value}
                                    onClick={() => onChange?.(item)}
                                >
                                    {item.text}
                                </button>
                            </li>
                        ))}
                    </ul>
                )}
            </div>
            {subtext !== undefined && (
                <small className={clsx(styles.subText, isError && styles.isError)}>{subtext}</small>
            )}
        </>
    );
};

export default Select;
