import React, { useLayoutEffect, useState } from 'react';

import clsx from 'clsx';

import { errorHandlers } from '../../helpers/errorHandlers';
import { isServer } from '../../helpers/isServer';

import {
  makeHelpRequestAuthorized,
  makeHelpRequestUnauthorized,
} from '../../features/Helpdesk/endpoints';
import { HelpRequestUnauthorized } from '../../types';
import { useNotification } from '../../notifications';
import {
  extensionError,
  sizeError,
  VALID_IMAGE_EXTENSIONS,
  VALID_VIDEO_EXTENSIONS,
} from '../../features/Post/constants';
import { getFileExtension } from '../../features/Post/helpers';
import { userUser1f } from '../../f1user';

import { Button, Checkbox, Input, Modal, Textarea, UploadFile } from '../../ui';

import styles from './HelpModal.module.scss';

import { CloseIcon } from '../../assets/icons';

//     
const IMAGE_MAX_SIZE = 200;

const DEFAULT_DATA: HelpRequestUnauthorized = {
  name: '',
  email: '',
  theme: '',
  message: '',
  recordWebInfo: '',
};

export type HelpModalProps = {
  isOpened: boolean;
  onClose(): void;
};
export const HelpModal = (props: HelpModalProps) => {
  const { isOpened, onClose } = props;
  const { user, isAuthorized } = userUser1f();
  const [isLoading, setIsLoading] = useState(false);
  const [isAddWebInfo, setIsAddWebInfo] = useState(false);
  const [isWebInfoVisible, setIsWebInfoVisible] = useState(false);
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [data, setData] = useState<HelpRequestUnauthorized>({
    name: user?.name || '',
    email: user?.email || '',
    theme: '',
    message: '',
    recordWebInfo: '',
  });
  const notifications = useNotification();

  const confirm = () => {
    if (isLoading) return;

    setIsLoading(true);

    const payload = new FormData();

    payload.append('theme', data.theme);
    payload.append('message', data.message);

    if (isAddWebInfo && !isServer()) {
      payload.append(
        'recordWebInfo',
        `*Location*: ${window.location.href};
                      *User-Agent*: ${window.navigator.userAgent};
                      *Screen Resolution*: ${window.innerWidth} x ${window.innerHeight};`
      );
    }

    if (data.files !== undefined) {
      data.files.forEach((file) => {
        payload.append('files[]', file);
      });
    }

    data.name && payload.append('name', data.name);
    data.email && payload.append('email', data.email);

    const method = isAuthorized
      ? makeHelpRequestAuthorized
      : makeHelpRequestUnauthorized;

    method(payload)
      .then((response) => {
        if (response.success) {
          notifications.success('  ');
          onClose();
        } else {
          if (response.errors !== undefined) {
            setErrors(errorHandlers(response));
          }

          notifications.error(
            response.message ?? '   '
          );
        }
      })
      .catch(() => notifications.error('   '))
      .finally(() => {
        setIsLoading(false);
        onClose();
      });
  };

  const uploadHandler = (files: File[]) => {
    const invalidSizeFileNames: string[] = [];
    const invalidExtensionFileNames: string[] = [];

    const filteredFiles = files.filter((file) => {
      const extension = getFileExtension(file);

      const isSizeValid = file.size / 1000000 < IMAGE_MAX_SIZE;
      const isExtensionValid =
        extension !== null &&
        [...VALID_IMAGE_EXTENSIONS, ...VALID_VIDEO_EXTENSIONS].includes(
          extension.toLowerCase()
        );

      if (!isSizeValid) invalidSizeFileNames.push(file.name);
      if (!isExtensionValid) invalidExtensionFileNames.push(file.name);

      return isSizeValid && isExtensionValid;
    });

    setErrors((prevErrors) => {
      if (
        invalidSizeFileNames.length > 0 ||
        invalidExtensionFileNames.length > 0
      ) {
        return {
          ...prevErrors,
          'files.0': `${
            invalidSizeFileNames.length === 0
              ? ''
              : `${sizeError('200 ', invalidSizeFileNames)};`
          } ${
            invalidExtensionFileNames.length === 0
              ? ''
              : `${extensionError(
                  [...VALID_IMAGE_EXTENSIONS, ...VALID_VIDEO_EXTENSIONS],
                  invalidExtensionFileNames
                )}`
          }`,
        };
      }

      delete prevErrors['files.0'];

      return prevErrors;
    });

    if (filteredFiles.length === 0) return;

    setData((prevData) => ({
      ...prevData,
      files: [...(prevData.files ?? []), ...filteredFiles],
    }));
  };

  useLayoutEffect(() => {
    if (!user?.name) return;

    setData((prevData) => ({
      ...prevData,
      email: user.email ?? '',
      name: user.name ?? '',
    }));
  }, [user]);

  useLayoutEffect(() => {
    if (isOpened) return;

    setData({
      ...DEFAULT_DATA,
      name: user?.name || '',
      email: user?.email || '',
    });
    setErrors({});
  }, [isOpened]);

  console.log({ isAuthorized, data });

  return (
    <Modal
      isOpened={isOpened}
      onClose={onClose}
      title=" "
      classes={{ root: styles.root }}
    >
      <div className={styles.content}>
        <p className={styles.description}>
          ,     
        </p>

        <fieldset className={styles.fieldset}>
          <Input
            id="Help theme"
            label="  *"
            value={data.theme}
            onChange={({ target }) =>
              setData((prevData) => ({ ...prevData, theme: target.value }))
            }
            isError={errors.theme !== undefined}
            subtext={errors.theme}
            classes={{ input: styles.theme }}
          />

          <Textarea
            label=" "
            value={data.message}
            onChange={({ target }) =>
              setData((prevData) => ({ ...prevData, message: target.value }))
            }
            isError={errors.message !== undefined}
            subtext={errors.message}
            classes={{ input: styles.description }}
          />

          <div>
            <div
              className={clsx(
                styles.uploadWrapper,
                errors['files.0'] !== undefined && styles.isError
              )}
            >
              <UploadFile
                size="small"
                inputStyle="primary"
                title=" "
                accept={[
                  ...VALID_IMAGE_EXTENSIONS.map(
                    (extension) => `image/${extension}`
                  ),
                  ...VALID_VIDEO_EXTENSIONS.map(
                    (extension) => `video/${extension}`
                  ),
                ].join(', ')}
                onUpdate={uploadHandler}
                isDisabled={(data.files?.length ?? 0) > 0}
                classes={{ root: styles.upload, icon: styles.uploadIcon }}
              />

              {data.files !== undefined && data.files.length > 0 && (
                <span className={styles.fileName}>{data.files[0].name}</span>
              )}

              <button
                type="button"
                className={styles.deleteFileButton}
                onClick={() => {
                  setData((prevData) => ({ ...prevData, files: undefined }));

                  setErrors((prevErrors) => {
                    delete prevErrors['files.0'];

                    return prevErrors;
                  });
                }}
              >
                <CloseIcon />
              </button>
            </div>
            {errors['files.0'] !== undefined && (
              <small className={styles.errorText}>{errors['files.0']}</small>
            )}
          </div>
        </fieldset>

        <fieldset className={styles.fieldset}>
          <Input
            id="Help name"
            label=" *"
            value={data.name}
            onChange={({ target }) =>
              setData((prevData) => ({ ...prevData, name: target.value }))
            }
            isError={errors.name !== undefined}
            subtext={errors.name}
            classes={{ input: styles.name }}
          />

          <Input
            id="Help email *"
            label="  e-mail *"
            value={data.email}
            onChange={({ target }) =>
              setData((prevData) => ({ ...prevData, email: target.value }))
            }
            isError={errors.email !== undefined}
            subtext={errors.email}
            classes={{ input: styles.email }}
          />
        </fieldset>

        <div className={styles.webInfoBlock}>
          <Checkbox
            isChecked={isAddWebInfo}
            onChange={setIsAddWebInfo}
            className={styles.webInfoCheckbox}
          />

          <div className={styles.webInfoTextBlock}>
            <p className={styles.webInfoText}>
                   , ,    
              URL .       .
            </p>

            <button
              type="button"
              className={styles.webInfoQuestion}
              onClick={() => setIsWebInfoVisible((isVisible) => !isVisible)}
            >
                     ?
            </button>

            {isWebInfoVisible && (
              <div className={styles.webInfo}>
                *Location*: {window.location.href} <br />
                *User-Agent*: {window.navigator.userAgent} <br />
                *Screen Resolution*: {window.innerWidth} x {window.innerHeight}
                <br />
              </div>
            )}
          </div>
        </div>

        <div className={styles.actions}>
          <Button text="" onClick={confirm} isLoading={isLoading} />
          <Button buttonStyle="link" text="" onClick={onClose} />
        </div>
      </div>
    </Modal>
  );
};
