import clsx from 'clsx';

import styles from './Badge.module.scss';

type BadgeProps = {
    title: string | number;
    isActive?: boolean;
};

/**
 * Badges
 * @description https://wind-ui.com/components/badges/
 * @link https://wind-ui.com/components/badges/
 * @param title
 * @param description
 * @constructor
 */
const Badge = ({ title, isActive }: BadgeProps) => {
    return (
        <span className={clsx(styles.badge, isActive ? styles.badgeActive : styles.badgeInactive)}>
            {title}
        </span>
    );
};
export default Badge;
