import React from 'react';

import clsx from 'clsx';

import { TPostStatus } from '@/features/Posts/types';

import { TTab } from '@/ui/Tabs/types';

import styles from './Tabs.module.scss';

type TabsProps = {
    tabs: TTab[];
    selected?: string | number;
    onClick: (nextTab: TPostStatus) => void;
};

/**
 * Tabs
 * @description Tabs organize content across different screens, data sets, and other interactions
 * @link https://wind-ui.com/components/tabs/
 * @param tabs
 * @param onClick
 * @param selected
 * @constructor
 */
const Tabs = ({ tabs, onClick, selected = 1 }: TabsProps) => {
    return (
        <nav className={styles.tabWrapper} role="tablist">
            {tabs.map((tab) => {
                if (tab.isHidden) return null;
                return (
                    <button
                        key={tab.id}
                        type="button"
                        id={`tab-label-${tab.id}`}
                        className={clsx(
                            styles.tabButton,
                            selected !== tab.id ? styles.buttonDefault : styles.buttonActive,
                        )}
                        onClick={() => onClick(tab.id)}
                    >
                        {tab.icon}
                        <span>{tab.title}</span>
                    </button>
                );
            })}
        </nav>
    );
};

export default Tabs;
