import React, { ChangeEvent } from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import { TComponentSizes } from '../../ui/types';

import styles from './UploadFile.module.scss';

import { UploadIcon } from '../../assets/icons';

import buttonStyles from '../Button/Buttons.module.scss';

type UploadInputStyle =
  | 'primary'
  | 'secondary'
  | 'outline'
  | 'link'
  | 'gray'
  | 'graySecondary';

export type ClassKey = 'root' | 'icon';

const mapSizeToClass: Record<TComponentSizes, string> = {
  large: buttonStyles.buttonSizeLarge,
  base: buttonStyles.buttonSizeBase,
  small: buttonStyles.buttonSizeSmall,
};

const mapStyleToClass: Record<UploadInputStyle, string> = {
  link: buttonStyles.buttonLinkColor,
  outline: buttonStyles.buttonOutlineColor,
  primary: buttonStyles.buttonPrimaryColor,
  secondary: buttonStyles.buttonSecondaryColor,
  gray: buttonStyles.buttonGrayColor,
  graySecondary: buttonStyles.buttonGraySecondaryColor,
};

type Props = {
  title?: string;
  isMultiple?: boolean;
  accept?: string;
  maxFiles?: number;
  onUpdate: (files: File[]) => void;
  size?: TComponentSizes;
  inputStyle?: UploadInputStyle;
  classes?: Classes<ClassKey>;
  isDisabled?: boolean;
};

const UploadFile = ({
  title = ' ',
  isMultiple,
  accept = '*',
  maxFiles = 10,
  onUpdate,
  size = 'base',
  inputStyle = 'primary',
  classes,
  isDisabled = false,
}: Props) => {
  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.currentTarget.files === null) return;
    const files = e.currentTarget.files;
    const count =
      maxFiles !== undefined && maxFiles < files.length
        ? maxFiles
        : files.length;
    const update: File[] = [];
    for (let index = 0; index < count; index++) {
      update.push(files[index]);
    }
    if (update.length > 0) onUpdate(update);
    e.currentTarget.value = '';
  };

  return (
    <label
      className={clsx(
        buttonStyles.button,
        mapSizeToClass[size],
        mapStyleToClass[inputStyle],
        styles.inputWrapper,
        classes?.root,
        isDisabled ? styles.disabled : null
      )}
    >
      <span className={clsx(buttonStyles.buttonIconWrapper, classes?.icon)}>
        <UploadIcon />
      </span>
      <span>{title}</span>
      <input
        id="files"
        type="file"
        defaultValue=""
        accept={accept}
        onChange={handleFileChange}
        multiple={isMultiple}
        name="files"
        disabled={isDisabled}
      />
    </label>
  );
};

export default UploadFile;
