import React, { CSSProperties, ReactNode } from 'react';

import { Classes } from '@/global-types';
import clsx from 'clsx';

import styles from './Tooltip.module.scss';

type TPositions = 'top' | 'bottom';

const mapPositionToClass: Record<TPositions, string> = {
    top: styles.isTop,
    bottom: styles.isBottom,
};

type ClassKey = 'root' | 'tooltip';

type TooltipProps = {
    tooltipContent: ReactNode;
    width?: number;
    children?: React.ReactNode;
    position?: TPositions;
    classes?: Classes<ClassKey>;
};

const Tooltip = ({
    tooltipContent,
    children,
    classes,
    position = 'top',
    width = 192,
}: TooltipProps) => {
    return (
        <div
            tabIndex={0}
            role="button"
            aria-label={typeof tooltipContent === 'string' ? tooltipContent : undefined}
            className={clsx(styles.root, classes?.root)}
        >
            {children}

            <span
                role="tooltip"
                style={{ '--width': `${width}px` } as CSSProperties}
                className={clsx(styles.tooltip, mapPositionToClass[position], classes?.tooltip)}
            >
                {tooltipContent}
            </span>
        </div>
    );
};

export default Tooltip;
