import { ReactNode } from 'react';

import clsx from 'clsx';

import { TComponentSizes } from '@/ui/types';

import styles from './ButtonIcon.module.scss';

import buttonStyles from '../Button/Buttons.module.scss';

type ButtonStyle = 'primary' | 'secondary' | 'link';

const mapStyleToClass: Record<ButtonStyle, string> = {
    link: buttonStyles.buttonLinkColor,
    primary: buttonStyles.buttonPrimaryColor,
    secondary: buttonStyles.buttonSecondaryColor,
};

const mapSizeToClass: Record<TComponentSizes, string> = {
    small: styles.sizeSmall,
    base: styles.sizeBase,
    large: styles.sizeLarge,
};

type ButtonIconProps = {
    id?: string;
    onClick: () => void;
    style?: ButtonStyle;
    size?: TComponentSizes;
    children?: ReactNode;
};

/**
 * Button
 * @description Button are interactive element activated by a user with a mouse, keyboard, or tap
 * @link https://wind-ui.com/components/buttons/
 * @param id
 * @param onClick
 * @param style
 * @param size
 * @param children
 * @constructor
 */
const ButtonIcon = ({
    id,
    onClick,
    style = 'primary',
    size = 'base',
    children,
}: ButtonIconProps) => {
    return (
        <button
            id={id}
            className={clsx(styles.buttonIcon, mapStyleToClass[style], mapSizeToClass[size])}
            onClick={onClick}
            type="button"
        >
            <i>{children}</i>
        </button>
    );
};

export default ButtonIcon;
